___CSS_LOADER_EXPORT___.push([module.id, `
.board {
  display: grid;
  width: max-content;
  height: max-content;
  grid-template-columns: repeat(10, 30px);
  grid-template-rows: repeat(10, 30px);
  gap: 2px;
  margin: 20px;
  background: #0e1a2b;
  padding: 5px;
  border: 2px solid #333;
  border-radius: 2px;
}

.cell {
  width: 30px;
  height: 30px;
  background: #1e3a5f;
  border-radius: 2px;
  cursor: pointer;
  transition: background 0.2s;
}

.cell:hover {
  background: #2f5d8a;
}

.cell.ship {
  background: #666; 
}

.cell.ship:hover {
    background: lightgray;
}

.cell.hit {
  background: crimson;
}
.cell.miss {
  background: lightblue;
}

#shipyard {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin: 12px 0;
}

.ship-piece {
  display: inline-flex;
  flex-direction: column;       
  align-items: center;
  cursor: grab;
  padding: 4px;
  user-select: none;
}

.ship-piece.dragging .rotate-btn {
  display: none;
}

.ship-body {
  display: flex;
}

.ship-piece.horizontal {
  flex-direction: row;
}

.ship-body.vertical {
  flex-direction: column;
}

.ship-cell {
  width: 30px;
  height: 30px;
  background: #666;
  border: 1px solid #333;
}

.rotate-btn {
  margin: 0 0 0 4px;
}

.ship-piece.vertical .rotate-btn {
  margin: 4px 0 0 0;            /* when vertical: top spacing */
}
